.slider-area {
  color: #fff;
  position: relative;
  text-align: center;  
  
}

.slider-content {
  padding: 180px 60px;
}

.nivo-caption::after {
  background: #444 none repeat scroll 0 0;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;   
}

.layer-1-2 {
  margin: 20px 0; 
}

.layer-1-1 h2 {
  color: black;
  font-size: 32px;
  font-weight: 900;
}

.layer-1-2 h1 {
  
  color: black;
  font-size: 50px;
  font-weight: 700;
  line-height: 60px;
}

.layer-1-3 {
  margin: 50px 0 0;    
}
.pic {
     transition: all 0.8s ease 0s;
}

.layer-1-3 a.ready-btn {
  border: 1px solid #fff;
  border-radius: 30px;
  color: black;
  cursor: pointer;
  display: inline-block;
  font-size: 17px;
  font-weight: 600;
  margin-top: 30px;
  padding: 10px 20px;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.8s ease 0s;
  z-index: 222;        
}

.layer-1-3 a.ready-btn:hover {
  color: black;
  background: #3EC1D5;
  border: 1px solid #3EC1D5;
  text-decoration: none;      
}

.ready-btn.right-btn {
  margin-right: 15px;
  background: #3EC1D5;
  border: 1px solid #3EC1D5 !important;
}

.ready-btn.right-btn:hover {
  background: transparent !important;
  border: 1px solid #fff !important;
}

.nivo-controlNav {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  z-index: 8;     
}

.nivo-directionNav {
  display: block;    
}

.nivo-controlNav a {
  background: transparent none repeat scroll 0 0;
  border: 2px solid #fff;
  box-shadow: none;
  display: inline-block;
  font-size: 0;
  height: 2px;
  margin: 5px 3px;
  opacity: 1;
  text-align: center;
  text-indent: inherit;
  vertical-align: top;
  width: 20px;        
}

.nivo-controlNav a:hover, .nivo-controlNav a.active {
  background: #fff none repeat scroll 0 0;
  border-color: #3EC1D5;
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);    
}

/* Normal desktop :992px. */

@media (min-width: 1400px) and (max-width:1920px) {
  .slider-content {
    padding: 340px 0;   
  }
}

/* Normal desktop :992px. */

@media (min-width: 992px) and (max-width: 1169px) {
  .slider-content {
    padding: 144px 0;    
  }
}

/* Tablet desktop :768px. */

@media (min-width: 768px) and (max-width: 991px) {
  .slider-content {
    padding: 93px 0;     
  }
  .layer-1-2 h1 {
    font-size: 22px;
    line-height: 24px;
  }
  .layer-1-3 a.ready-btn {
    font-size: 14px;
    margin-top: 20px;
    padding: 10px 20px;
  }
}

@media (max-width: 767px) {
  .nivo-directionNav {
    display: none;   
  }
  .slider-content {
    padding: 46px 0px;    
  }
  .layer-1-2 {
    margin: 10px 0;     
  }
  .layer-1-1 h2 {
    font-size: 14px;
    line-height: 16px;  
  }
  .layer-1-2 h1 {
    font-size: 14px;
    line-height: 16px;     
  }
  .layer-1-3 a.ready-btn {
    font-size: 12px;
    margin-top: 10px;
    padding: 8px 10px;     
  }
  .layer-1-3 {
    margin: 10px 0 0;     
  }
  .nivo-controlNav {
    bottom: -8px;         
  }
}

@media only screen and (min-width: 480px) and (max-width: 767px) {
  .slider-content {
    padding: 70px 0px;     
  }
  .layer-1-1 h2, .layer-1-2 h1 {
    font-size: 24px;
    line-height: 30px;      
  }
}

